/* Just cross hairs for debugging - can be removed upon release */

.croppie-container .cr-viewport.debug:before,
.croppie-container .cr-viewport.debug:after {
  background: white;
  width: 1px;
  height: 1px;
  content: '';
  position: absolute;
}

.croppie-container .cr-viewport.debug:before {
  top: 0;
  height: 100%;
  left: 50%;
}

.croppie-container .cr-viewport.debug:after {
  top: 50%;
  left: 0;
  width: 100%;
}


