  background-color: transparent;
  color: #4b595e;
  border: none;
  padding-left: 20px;
  font-size: 12px;
  cursor: pointer
}

.fc-event:hover {
  color: inherit;
  text-decoration: underline
}

.fc-event:before {
  display: block;
  width: 6px;
  height: 6px;
  background: #bcc7cd;
  position: absolute;
  content: '';
  top: 5px;
  left: 8px;
  border-radius: 50%
}

.fc-event.fc-event--red:before {
