  border-bottom: 1px solid #39c9f5;
  text-decoration: none !important
}

.fc-add-event {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  color: white;
  background: #39c9f5;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  font-weight: bold;
  visibility: hidden;
  z-index: 110;
  line-height: 1.3;
  display: inline-block
}

.fc-add-event:hover {
  text-decoration: none
}

.fc-add-event:after {
  content: '+'
}

.fc-add-event.is-visible {
